import React, { Component, StyleSheet } from 'react';
import {
    Image,
    View,
    Text,
    TextInput,
    Button,
} from './onerva';

import Header from './header';

var styles = require('./styles');
var colors = require('./colors');


export default class CaregiverListScene extends Component {
    constructor(props, context) {
	super(props, context);
    }

    render() {
	let scene = this;
	let dotColors = colors.rainbowList;
	let caregivers = scene.props.caregivers;
	if ( ! caregivers )
	    caregivers = [];

	var btnStyle = { flex: '1 1 auto', margin: '5pt', padding: '8pt', backgroundColor: '#ffffff', border: '2pt solid', borderColor: colors.turquoise, color: colors.turquoise };
	/*
	if ( scene.props.caregiverGroup.users ) {
	    scene.props.caregiverGroup.users.map(function(item, index) {
		caregivers.push(item);
	    });
	}
	*/
	    
	
	return (
	    <View style={{flex: '1 1 0%', display: 'flex', flexDirection: 'column'}}>
	    
	    <Header title={'Valitse hoitaja'} backScene={scene.props.backScene} onBack={scene.props.onBack} onLock={scene.props.onLock} />

	    <View style={{ margin: 0, overflowY: 'auto', flex: '1 1 0%' }}>
	    {
		caregivers.map(function(item, index) {
		    if ( ! item.alias ) return ( <View key={'userView'+item.uid+index} style={{ display: 'none' }}></View> )
		    
		    let initial = item.alias[0];
		    return (
			<View key={'userView'+item.uid+index} 
			style={{ width: '100%', margin: '0pt', padding: '0pt', backgroundColor: colors.background, borderBottom: '1px solid #f0f0f0' }}
			onPress={() => { scene.props.onSelect(item)}}>
			
			<View style={{display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'stretch'}}>
			
			<View style={{opacity: 0.8, margin: '8pt', marginLeft: '10pt', borderRadius: '20pt', height: '40pt', width: '40pt', backgroundColor: dotColors[index % dotColors.length], alignItems: 'center', justifyContent: 'center', display: 'flex' }} >
			<Text style={{ color: '#ffffff', fontSize: '18pt'}}>
			{initial}
			</Text>
			</View>

			<Button key={'userBtn'+index}
			style={{flex: '1 1 0%', display: 'flex', alignItems: 'center', paddingLeft: '20pt', textAlign: 'left', backgroundColor: colors.background }}
			onPress={() => {scene.props.onSelect(item)}}>
			<View>
			<Text style={{color: '#404040', fontSize: '12pt' }}>
			{item.alias}
			</Text>
			</View>

			</Button>
			
			<View style={{display: 'flex', alignItems: 'center', paddingRight: '10pt'}}>
			<Image style={{width: '15pt', height: '15pt'}} source={require('./imgs/next.png')} />
			</View>
			
			</View>
			
			</View>
		    )
		})
	    }
	    </View>

	    <Button style={btnStyle}
	    onPress={() => { scene.props.onAddCaregiver() }}>
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt' }}>
	    LIS HOITAJA
	    </Text>
	    </Button>
	    

	    {/*
	    <View style={{ margin: 0, overflowY: 'auto', flex: 1 }}>
	    {
		scene.props.chats.map(function(item, index) {
		    let initial = item.alias[0];
		    return (
			<Button key={'chatBtn'+index} 
			style={{ width: '100%', margin: '0pt', padding: '8pt', backgroundColor: colors.background, borderBottom: '1px solid #f0f0f0' }}
			onPress={() => { scene.props.onSelect(item)}}>
			
			<View style={{display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'stretch'}}>
			
			<View style={{opacity: 0.8, margin: '3pt', marginLeft: '10pt', borderRadius: '20pt', height: '40pt', width: '40pt', backgroundColor: dotColors[index % dotColors.length], alignItems: 'center', justifyContent: 'center', display: 'flex'}} >
			<Text style={{ color: '#ffffff', fontSize: '18pt'}}>
			{initial}
			</Text>
			</View>

			<View style={{flex: '1 1 0%', display: 'flex', alignItems: 'center', paddingLeft: '20pt' }}>
			<Text style={{color: '#404040', fontSize: '12pt' }}>
			{item.alias}
			</Text>
			</View>

			<View style={{display: 'flex', alignItems: 'center', paddingRight: '10pt'}}>
			<Image style={{width: '15pt', height: '15pt'}} source={require('./imgs/next.png')} />
			</View>
			
			</View>
			
			</Button>
		    )
		})
	    }
	    </View>
	      */}

	   
	    </View>
	)
    }
}
    
