import React, { Component, StyleSheet } from 'react';
import {
    Image,
    View,
    Text,
    Button,
} from './onerva';

var styles = require('./styles');
var colors = require('./colors');

import Header from './header';

export default class ErrorScene extends Component {
    constructor(props, context) {
	super(props, context);
    }

    render() {
	let title = this.props.title;
	let scene = this;

	let error1 = 'Onerva kohtasi ongelman, jota se ei pysty ratkaisemaan. Ongelman syy saattaa olla tietoverkkoyhteyksiss tai muissa teknisiss yksityiskohdissa. Jos ongelma toistuu, ota yhteys tukeen ja kerro alla oleva virhekoodi.';
	let error2 = this.props.error;

	if ( this.props.error == 'credentials' ) {
	    error1 = 'Valittu kyttj ja PIN-koodi eivt vastaa toisiaan. Jos ongelma toistuu, vaikka annat PIN-koodin varmasti oikein, ota yhteys tukeen.';
	    error2 = '';
	}
	if ( this.props.error.indexOf('NetworkError') >= 0 ) {
	    error1 = 'Onerva ei saa yhteytt palvelimeen. Ongelman syy saattaa olla tietoverkkoyhteyksiss tai muissa teknisiss yksityiskohdissa. Jos ongelma toistuu hyvn tietoverkkoyhteyden alueella, ota yhteys tukeen ja kerro alla oleva virhekoodi.';
	    error2 = this.props.error;    
	}

	let error3 = ( <div>Tukeen saat yhteyden shkpostilla <a style={{color: colors.darkGreen}} href='mailto:tuki@onervahoiva.fi'>tuki@onervahoiva.fi</a> tai soitamalla <a style={{color: colors.darkGreen}} href='tel:0456005991'>045 6005 991</a>.</div> );
	    
	return (
	    <View style={{display: 'flex', flexDirection: 'column', justifyContent: 'stretch', alignItems: 'stretch', flex: 1}}>

	    <Header title={'Ongelma'} backScene={scene.props.backScene} onBack={scene.props.onBack} onLock={scene.props.onLock} />

	    <View style={{ margin: 0, overflowY: 'auto', flex: 1 }}>
	    
	    <View style={{ padding: '20pt'}}>
	    <Text style={{ lineHeight: '130%', fontSize: '11pt'}}>
	    {error1}
	    </Text>	    
	    </View>

	    <View style={{ padding: '20pt'}}>
	    <Text style={{ lineHeight: '130%', fontSize: '11pt'}}>
	    {error2}
	    </Text>	    
	    </View>

	    <View style={{ padding: '20pt'}}>
	    <Text style={{ lineHeight: '130%', fontSize: '11pt'}}>
	    {error3}
	    </Text>	    
	    </View>

	    </View>

	    	<Button style={{ margin: '8pt', padding: '8pt', backgroundColor: colors.turquoise }}
	    onPress={() => { scene.props.onBack(scene.props.backScene) }}>
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt', color: colors.background }}>
		&lt; &nbsp; &nbsp; &nbsp; PALAA
	    </Text>
	    </Button>

	    </View>
	)

    }
}
    



