import React, { Component, StyleSheet } from 'react';
import {
    Image,
    View,
    Text,
    TextInput,
    Button,
} from './onerva';

import Header from './header';

var styles = require('./styles');
var colors = require('./colors');

const tick = require('./imgs/tick.png');

export default class MainScene extends Component {
    constructor(props, context) {
	super(props, context);
	this.state = {
	    username: props.username ? props.username : '',
	    password: props.password ? props.password : '',
	};
    }

    render() {
	let scene = this;
	let title = this.props.title;

	var buttonStyle = { flex: '1 1 auto', margin: '5pt', padding: '8pt', backgroundColor: '#ffffff', border: '2pt solid', borderColor: colors.turquoise, color: colors.turquoise };

	var disabledButtonStyle = { flex: '1 1 auto', margin: '5pt', padding: '8pt', backgroundColor: '#ffffff', border: '2pt solid', borderColor: colors.turquoise, color: colors.turquoise, opacity: '0.3' };

	var hiddenButtonStyle = { display: 'none', flex: '1 1 auto', margin: '5pt', padding: '8pt', backgroundColor: '#ffffff', border: '2pt solid', borderColor: colors.turquoise, color: colors.turquoise, opacity: '0.3' };

	var btnAddPatientStyle   = scene.props.isAdmin ? buttonStyle : hiddenButtonStyle;
	var btnShowPatientsStyle = scene.props.isAdmin ? buttonStyle : hiddenButtonStyle;
	var btnBroadcastStyle    = scene.props.isAdmin ? buttonStyle : hiddenButtonStyle;
	
	var btnAddCaregiverStyle   = scene.props.isAdmin ? buttonStyle : hiddenButtonStyle;
	var btnShowCaregiversStyle = scene.props.isAdmin ? buttonStyle : hiddenButtonStyle;
	var btnShowReportStyle     = scene.props.isAdmin ? disabledButtonStyle : hiddenButtonStyle;

	var btnChangePasswordStyle = buttonStyle;

	
	return (
	    <View style={{flex: '1 1 0%', display: 'flex', flexDirection: 'column'}}>

	    <Header title={'Hallintapaneeli'} backScene={scene.props.backScene} onBack={scene.props.onBack} onLock={scene.props.onLock} />

	    <View style={{ margin: 0, overflowY: 'auto', flex: 1 }}>
	    
	    <View style={{ flex: '1 1 0%', display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', alignItems: 'stretch', margin: '10pt' }} >

	    	    
	    <Text>&nbsp;</Text>

	    { /*
	    <Button style={btnAddPatientStyle}
	    onPress={() => { scene.props.onAddPatient() }}>
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt' }}>
	    LIS HOIVA-ASIAKAS
	    </Text>
	    </Button>
	      */ }

	    <Button style={btnShowPatientsStyle}
	    onPress={() => { scene.props.onShowPatients() }}>
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt' }}>
	    HOIVA-ASIAKKAAT
	    </Text>
	    </Button>

	    { /*
	    <Button style={btnAddCaregiverStyle}
	    onPress={() => { scene.props.onAddCaregiver() }}>
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt' }}>
	    LIS HOITAJA
	    </Text>
	    </Button>
	      */ }
	    
	    <Button style={btnShowCaregiversStyle}
	    onPress={() => { scene.props.onShowCaregivers() }}>
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt' }}>
	    HOITAJAT
	    </Text>
	    </Button>


	    <Text style={{ margin: '5pt' }} >&nbsp;</Text>

	    <Button style={btnBroadcastStyle}
	    onPress={() => { scene.props.onBroadcastMessage() }}>
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt' }}>
	    VIESTI OMAISILLE
	    </Text>
	    </Button>

	    <Button style={btnShowReportStyle}
	    onPress={() => { scene.props.onShowReport() }}>
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt' }}>
	    RAPORTTI
	    </Text>
	    </Button>

	    <Text style={{ margin: '5pt' }} >&nbsp;</Text>
	    
	    <Button style={btnChangePasswordStyle}
	    onPress={() => { scene.props.onChangePassword() }}>
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt' }}>
	    VAIHDA SALASANA
	    </Text>
	    </Button>

	    <Button style={buttonStyle}
	    onPress={() => { scene.props.onLock() }}>
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt' }}>
	    KIRJAUDU ULOS
	    </Text>
	    </Button>

    
	    </View>

	    </View>
	    
	    </View>
	)

    }
}
    



