import React, { Component, StyleSheet } from 'react';
import {
    Image,
    View,
    Text,
    Button,
} from './onerva';

var styles = require('./styles');
var colors = require('./colors');

const backIcon = require('./imgs/back.png');
const logoutIcon = require('./imgs/logout.png');

export default class Header extends Component {
    constructor(props, context) {
	super(props, context);
    }

    render() {
	let scene = this;
	let title = this.props.title;
	let hasBack = this.props.onBack;
	let hasLock = this.props.onLock;
	
	return (
	    <View style={{display: 'flex', flexDirection: 'column' }}>
	    <View style={{height: '10pt', width: '100%', backgroundColor: colors.green2, }} />

	    <View style={{height: '40pt', backgroundColor: colors.green, display: 'flex', alignItems: 'center' }}>
	    
	    <Button
	    style={{opacity: hasBack ? 1.0 : 0.0, width: '40pt', height: '40pt', backgroundColor: colors.green}}
	    underlayColor={colors.green2}
	    onPress={() => { console.log('back'); scene.props.onBack(scene.props.backScene); }}
	    >
	    <View style={{display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center'}}>
	    <Image source={backIcon} style={{marginTop: '5pt', width: '18pt', height: '18pt'}}/>
	    <Text style={{ height: '10pt', color: colors.background, fontWeight: 'bold', fontSize: '7pt' }}>PALAA</Text>
	    </View>
	    </Button>

	    <View style={{ flex: 1, color: colors.background, fontWeight: 'normal', fontSize: '18pt', marginTop: '0pt', marginLeft: '5pt' }}>
	    <Text>{this.props.title}</Text>
	    </View>

	    <Button
	    style={{opacity: hasLock ? 1.0 : 0.0, width: '40pt', height: '40pt', backgroundColor: colors.green}}
	    underlayColor={colors.green2}
	    onPress={() => { console.log('lock'); scene.props.onLock(); }}
	    >
	    <View style={{display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center'}}>
	    <Image source={logoutIcon} style={{marginTop: '5pt', width: '18pt', height: '18pt'}}/>
	    <Text style={{ height: '10pt', color: colors.background, fontWeight: 'bold', fontSize: '7pt' }}>POISTU</Text>
	    </View>
	    </Button>

	    </View>
	    
	    </View>
	)

    }
}
    



