import React, { Component, StyleSheet } from 'react';
import {
    Image,
    View,
    Text,
    TextInput,
    Button,
} from './onerva';

import Header from './header';

var styles = require('./styles');
var colors = require('./colors');

var onerva = { util: require('../util.js') };

const tick = require('./imgs/tick.png');

export default class ChangePasswordScene extends Component {
    constructor(props, context) {
	super(props, context);
	this.state = {
	    email: props.credentials.email ? props.credentials.email   : '',
	    password1: '',
	    password2: '',
	    pin1:   '',
	    pin2:   '',
	    rnd:   ("0000" + Math.floor(Math.random() * 10000)).slice(-4),
	    rnd2:  onerva.util.generateOnervaID().substring(0,14),
	};
	console.log(this.state);
    }

    render() {
	let scene = this;
	let title = this.props.title;
	let btnColor = colors.gray;
	
	let emailOk = (
	    ( scene.state.email.length > 0 ) &&
	    ( scene.state.email.indexOf('@') > 0 )
	);
	let passwordOk = (
	    ( scene.state.password1.length >= 8 ) &&
	    ( scene.state.password2.length >= 8 ) &&
	    ( scene.state.password1 == scene.state.password2 )
	);
	let pinOk = (
	    ( scene.state.pin1.length >= 4 ) &&
	    ( scene.state.pin2.length >= 4 ) &&
	    ( scene.state.pin1 == scene.state.pin2 )
	);

	if ( emailOk && passwordOk && pinOk )
	    btnColor = colors.turquoise;
	
	
	return (
	    <View style={{flex: '1 1 0%', display: 'flex', flexDirection: 'column'}}>

	    <Header title={scene.props.credentials.alias} backScene={scene.props.backScene} onBack={scene.props.onBack} onLock={scene.props.onLock} />

	    <View style={{ margin: 0, overflowY: 'auto', flex: 1 }}>
	    
	    <View style={{ flex: '1 1 0%', display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', alignItems: 'flex-start', margin: '20pt' }} >
	    
		
	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.email}
	    onChangeText={(text) => {scene.setState({ email: text })}}
	    placeholder='Anna hoitajan shkposti'
	    />
	    

	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.password1}
	    onChangeText={(text) => {scene.setState({ password1: text })}}
	    placeholder={'Anna uusi salasana, esim. ' + scene.state.rnd2 + ''}
	    />

	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.password2}
	    onChangeText={(text) => {scene.setState({ password2: text })}}
	    placeholder={'Anna uusi salasana uudestaan.'}
	    />

	    
	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.pin1}
	    onChangeText={(text) => {scene.setState({ pin1: text })}}
	    placeholder={'Anna uusi PIN, esim. ' + scene.state.rnd  + ''}
	    onKeyPress={(event) => {
		//if ( event.charCode == 13 )
		//    scene.props.onLogin(scene.state.username, scene.state.password);
	    }}
	    />

	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.pin2}
	    onChangeText={(text) => {scene.setState({ pin2: text })}}
	    placeholder={'Anna uusi PIN uudestaan.'}
	    onKeyPress={(event) => {
		//if ( event.charCode == 13 )
		//    scene.props.onLogin(scene.state.username, scene.state.password);
	    }}
	    />


	    <Text style={{ fontSize: '10pt', fontWeight: 'bold', marginTop: '20pt', marginBottom: '10pt', display: ( ! passwordOk ) ? 'block' : 'none' }}>Anna uusi salasana kahdesti. Salasanan tulee olla vhintn 8 merkki pitk.</Text>
	    <Text style={{ fontSize: '10pt', fontWeight: 'bold', marginTop: '20pt', marginBottom: '10pt', display: ( ! pinOk ) ? 'block' : 'none' }}>Anna uusi PIN-kooodi kahdesti. PIN-koodin tulee olla 4 numeroa pitk.</Text>

	    </View>

	    </View>

	    
	    <Button style={{ margin: '8pt', padding: '8pt', backgroundColor: btnColor }}
	    onPress={() => {
		var email     = scene.state.email.trim();
		var password1 = scene.state.password1.trim();
		var pin1      = scene.state.pin1.trim();

		if ( emailOk && passwordOk && pinOk )
		    scene.props.onChangePassword(scene.props.credentials.uid,
						 scene.props.credentials.alias,
						 email, password1, pin1);
	    }}
	    >
	    
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt', color: colors.background }}>
	    TALLENNA &nbsp; &nbsp; &nbsp; &gt;
	    </Text>
	    </Button>

	    </View>
	)

    }
}
    



