import React, { Component, StyleSheet } from 'react';
import {
    Image,
    View,
    Text,
    TextInput,
    Button,
} from './onerva';

import Header from './header';

var styles = require('./styles');
var colors = require('./colors');

const tick = require('./imgs/tick.png');

export default class LoginScene extends Component {
    constructor(props, context) {
	super(props, context);
	this.state = {
	    email: props.email ? props.email : '',
	    password: props.password ? props.password : '',
	    pin: props.pin ? props.pin : '',
	};
    }

    render() {
	let scene = this;
	let title = this.props.title;

	return (
	    <View style={{flex: '1 1 0%', display: 'flex', flexDirection: 'column'}}>

	    <Header title={'Kirjautuminen'} backScene={scene.props.backScene} onBack={scene.props.onBack} onLock={scene.props.onLock} />

	    <View style={{ margin: 0, overflowY: 'auto', flex: 1 }}>
	    
	    <View style={{ flex: '1 1 0%', display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', alignItems: 'flex-start', margin: '20pt' }} >
	    
	    <Image style={{ width: '100pt', alignSelf: 'center' }} source={require('./imgs/splash.png')} />

	    <Text style={{ fontSize: '11pt', fontWeight: 'bold', marginTop: '20pt', marginBottom: '10pt'}}>TERVETULOA ONERVAN HALLINTAPANEELIIN</Text>

	    <Text style={{ width: '100%', fontSize: '11pt', lineHeight: '130%', textAlign: 'justify' }}></Text>
		
	    {/*
	    <Text style={{ fontSize: '10pt', fontWeight: 'bold', marginTop: '20pt', marginBottom: '10pt'}}>ONERVA-TUNNUKSESI</Text>
	      */}
	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.email}
	    onChangeText={(text) => {scene.setState({ email: text})}}
	    placeholder='Anna shkpostiosoitteesi thn'
		/>
		
	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    password={true}
	    value={scene.state.password}
	    onChangeText={(text) => {scene.setState({ password: text });}}
	    placeholder='Anna salasanasi thn'
	    />

	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    password={true}
	    value={scene.state.pin}
	    onChangeText={(text) => {scene.setState({ pin: text });}}
	    placeholder='Anna PIN-koodisi thn'
	    onKeyPress={(event) => {
		if ( event.charCode == 13 )
		    scene.props.onLogin(scene.state.email, scene.state.password, scene.state.pin);
	    }}
	    />

    
	    </View>

	    </View>

	    <Button style={{ margin: '8pt', padding: '8pt', backgroundColor: colors.turquoise }}
	    onPress={() => { scene.props.onLogin(scene.state.email, scene.state.password, scene.state.pin) }}>
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt', color: colors.background }}>
	    KIRJAUDU &nbsp; &nbsp; &nbsp; &gt;
	    </Text>
	    </Button>
	    
	    </View>
	)

    }
}
    



