/*

   show update patient
   1) fetch chat with cid (cid, alias, description, actions)
   2) fetch chat's gids (relatives)
   3) show

   update patient
   1) add patient (cid, alias, description, actions)
   2) update caregiver group (alias)
   3) update relative groups (alias)
   4) update relatives as users (alias, email)
   5) add relatives as relatives (gid, alias, email, pin)
   6) relative alias and email is empty, what to do? remove?

   remove patient
   1) update caregiver group (remove cid)
   3) leave relatives gids?

*/
