import React, { Component, StyleSheet } from 'react';
import {
    Image,
    View,
    Text,
    TextInput,
    Button,
} from './onerva';

import update from 'immutability-helper';

import Header from './header';

var styles = require('./styles');
var colors = require('./colors');

const tick = require('./imgs/tick.png');

export default class UpdatePatientScene extends Component {
    constructor(props, context) {
	super(props, context);	
	
	let actions = props.patient.actions;
	if ( actions ) {
	    var n = 0;
	    for( ; n < actions.length; ++n)
		if ( actions[n] == '' )
		    break;
	    actions[n]   = '';
	    actions[n+1] = '';
	    actions[n+2] = '';
	}

	this.updateRelatives = this.updateRelatives.bind(this);
	let relatives = this.updateRelatives(props);

	this.state = {
	    cid:         props.patient.cid ? props.patient.cid : '',
	    alias:       props.patient.alias ? props.patient.alias : '',
	    description: props.patient.description ? props.patient.description.message : '',
	    relatives:   relatives,
	    actions:     actions,
	}
    }

    componentWillReceiveProps(nextProps) {
	let relatives = this.updateRelatives(nextProps);
	this.setState({ relatives: relatives });
    }

    updateRelatives(props) {
	let relatives = [];
	if ( relatives ) {
	    var n = 0;
	    for(let i = 0; i < props.patient.relatives.length; ++i)
		if ( props.patient.relatives[i].chats.length > 0 ) {
		    relatives[n] = props.patient.relatives[i];
		    n += 1;
		}
	    relatives.push({
		alias: '',
		gid: null,
		users: [],
		chats: [ props.patient ]
	    });
    	    relatives.push({
		alias: '',
		gid: null,
		users: [],
		chats: [ props.patient ]
	    });
	}
	else {
	    relatives = [];
	    for(let i = 0; i < 10; ++i)
		relatives.push({
		    alias: '',
		    gid: null,
		    users: [],
		    chats: [ props.patient ]
		});
	}
	for(let i = 0; i < relatives.length; ++i) {
	    if ( relatives[i].users[0] && relatives[i].users[0].email )
		relatives[i].email = relatives[i].users[0].email;
	    else
		relatives[i].email = '';
	}
	console.log(relatives);

	return relatives;
    }

    
    render() {
	let scene = this;
	let title = this.props.title;
	let bgColor = ( scene.state.alias.length > 0 ) ? colors.turquoise : colors.gray;

	//console.log(scene.state.relatives);
	
	return (
	    <View style={{flex: '1 1 0%', display: 'flex', flexDirection: 'column'}}>

	    <Header title={'Hoiva-asiakas'} backScene={scene.props.backScene} onBack={scene.props.onBack} onLock={scene.props.onLock} />

	    <View style={{ margin: 0, overflowY: 'auto', flex: 1 }}>
	    
	    <View style={{ flex: '1 1 0%', display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', alignItems: 'flex-start', margin: '20pt' }} >

	    
	    <Text style={{fontSize: '8pt', marginTop: '16pt', marginBottom: '8pt' }}>NIMI JA KUVAUS</Text>
	    
	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.alias}
	    onChangeText={(text) => {scene.setState({ alias: text }); console.log(text)}}
	    placeholder='Anna hoiva-asiakkaan nimi'
	    />

	    <TextInput style={{height: '100pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', fontFamily: 'sans-serif', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.description}
	    onChangeText={(text) => {
		scene.setState({ description: text });
	    }}
	    multiline={true}
	    placeholder='Anna hoiva-asiakkaan kuvaus'
	    />


	    <Text style={{fontSize: '8pt', marginTop: '24pt', marginBottom: '8pt' }}>HOIVATOIMET</Text>
	    
	    {
		scene.state.actions.map(function(item,index) {
		    return (		
			<TextInput key={'actions'+index} style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
			value={scene.state.actions[index]}
			onChangeText={(text) => {
			    let actions = scene.state.actions;
			    actions[index] = text;
			    scene.setState(update(scene.state,
						  { actions: { $set: actions } }));
			}}
			placeholder={'Anna ' + (index+1) +'. hoivatoimi'}
			/>
		    )
		})
	    }


	    <Text style={{fontSize: '8pt', marginTop: '16pt', marginBottom: '8pt' }}>OMAISET</Text>

	    {
		scene.state.relatives.map(function(item,index) {
		    return (
			<View  key={'rel'+index} style={{ marginBottom: '20pt', display: ( index == 0 || ( index > 0 && scene.state.relatives[index-1].email.length > 0 ) ) ? 'block' : 'none' }}>
			
			<TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + (scene.state.relatives[index].gid ? colors.gray : colors.turquoise), borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
			value={scene.state.relatives[index].alias}
			onChangeText={(text) => {
			    if ( scene.state.relatives[index].gid )
				return;
			    let relatives = scene.state.relatives;
			    relatives[index].alias = text;
			    scene.setState(update(scene.state,
						  { relatives: { $set: relatives } }));
			}}
			placeholder={'Anna ' + (index+1) +'. omaisen nimi'}
			/>

			<TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + (scene.state.relatives[index].gid ? colors.gray : colors.turquoise), borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
			value={scene.state.relatives[index].email}
			onChangeText={(text) => {
			    if ( scene.state.relatives[index].gid )
				return;
			    let relatives = scene.state.relatives;
			    relatives[index].email = text;
			    scene.setState(update(scene.state,
						  { relatives: { $set: relatives } }));
			}}
			placeholder={'Anna ' + (index+1) +'. omaisen shkposti'}
			/>
			

			<Button style={{ margin: '0pt', padding: '4pt', backgroundColor: colors.darkRed, display: scene.state.cid.length > 0 && scene.state.relatives[index].gid ? 'block' : 'none' }}
			onPress={() => {
			    scene.props.onRemoveRelative(scene.state.relatives[index].gid, scene.state.cid);
			    /*
			    let relatives = scene.state.relatives;
			    relatives = relatives.splice(index,1);
			    scene.setState({ relatives: relatives });
			    */
			}}
			>
			
			<Text style={{ fontWeight: 'bold', fontSize: '10pt', color: colors.background, display: scene.state.cid.length > 0 ? 'block': 'none' }}>
			POISTA OMAINEN
			</Text>
			
			</Button>

			</View>
		    )
		})
	    }
		    
	    </View>

	    </View>

	    <Button style={{ margin: '16pt', padding: '8pt', backgroundColor: colors.darkRed, display: scene.state.cid.length > 0 ? 'block' : 'none' }}
	    onPress={() => {
		scene.props.onRemovePatient(scene.state.cid);
	    }}
	    >
	    
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt', color: colors.background, display: scene.state.cid.length > 0 ? 'block': 'none' }}>
	    POISTA HOIVA-ASIAKAS 
	    </Text>
	    
	    </Button>

	    
	    <Button style={{ margin: '8pt', padding: '8pt', backgroundColor: bgColor }}
	    onPress={() => {
		let actions = [];
		scene.state.actions.map(function(item,index) {
		    if ( item.trim().length > 0 )
			actions.push(item);
		});
		let new_relatives = [];
		scene.state.relatives.map(function(item,index) {
		    if ( item.email.trim().length > 0 && ! item.gid )
			new_relatives.push({ alias: item.alias, email: item.email });
		});

		console.log(JSON.stringify({alias: scene.state.alias, desc: scene.state.description, actions: actions, new_relatives: new_relatives}));
		scene.props.onUpdate(scene.state.cid, scene.state.alias, scene.state.description, actions, new_relatives);
	    }}
	    >
	    
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt', color: colors.background }}>
	    TALLENNA &nbsp; &nbsp; &nbsp; &gt;
	    </Text>
	    </Button>
	    
	    </View>
	)

    }
}
    



