import React, { Component, StyleSheet } from 'react';
import {
    Image,
    View,
    Text,
    TextInput,
    Button,
} from './onerva';

import Header from './header';

var styles = require('./styles');
var colors = require('./colors');

var onerva = { util: require('../util.js') };

const tick = require('./imgs/tick.png');

export default class UpdateCaregiverScene extends Component {
    constructor(props, context) {
	super(props, context);
	this.state = {
	    uid:   props.caregiver.uid ? props.caregiver.uid : '',
	    alias: props.caregiver.alias ? props.caregiver.alias : '',
	    email: props.caregiver.email ? props.caregiver.email   : '',
	    password: props.caregiver.password ? props.caregiver.password   : '',
	    pin:   '',
	    rnd:   ("0000" + Math.floor(Math.random() * 10000)).slice(-4),
	    rnd2:  onerva.util.generateOnervaID().substring(0,14),
	};
	console.log(this.state);
    }

    render() {
	let scene = this;
	let title = this.props.title;
	let btnColor = ( ( scene.state.alias.length > 0 ) && ( scene.state.email.length > 0 ) ) ? colors.turquoise : colors.gray;

	
	return (
	    <View style={{flex: '1 1 0%', display: 'flex', flexDirection: 'column'}}>

	    <Header title={'Hoitaja'} backScene={scene.props.backScene} onBack={scene.props.onBack} onLock={scene.props.onLock} />

	    <View style={{ margin: 0, overflowY: 'auto', flex: 1 }}>
	    
	    <View style={{ flex: '1 1 0%', display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', alignItems: 'flex-start', margin: '20pt' }} >
	    
		
	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.alias}
	    onChangeText={(text) => {scene.setState({ alias: text })}}
	    placeholder='Anna hoitajan nimi'
	    />
	    
	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.email}
	    onChangeText={(text) => {scene.setState({ email: text })}}
	    placeholder='Anna hoitajan shkposti'
	    />
	    

	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.password}
	    onChangeText={(text) => {scene.setState({ password: text })}}
	    placeholder={'Anna hoitajan uusi salasana, esim. ' + scene.state.rnd2 + ' tai jt tyhjksi.'}
	    />

	    
	    <TextInput style={{height: '25pt', width: '100%', backgroundColor: colors.background, fontSize: '10pt', fontWeight: 'normal', marginTop: '5pt', marginBottom: '10pt', border: '1px solid ' + colors.turquoise, borderTop: 0, borderRight: 0, borderLeft: 0, paddingLeft: '5pt', paddingRight: '5pt'}}
	    value={scene.state.pin}
	    onChangeText={(text) => {scene.setState({ pin: text })}}
	    placeholder={'Anna hoitajan uusi PIN, esim. ' + scene.state.rnd  + ' tai jt tyhjksi'}
	    onKeyPress={(event) => {
		//if ( event.charCode == 13 )
		//    scene.props.onLogin(scene.state.username, scene.state.password);
	    }}
	    />


	    <Text style={{ fontSize: '10pt', fontWeight: 'bold', marginTop: '20pt', marginBottom: '10pt', display: ( scene.props.caregiver && scene.props.caregiver.uid ) ? 'block' : 'none' }}>Huom. Jos muutat shkpostiosoitetta, salasanaa tai PIN-koodia, sinun tytyy antaa mys kaikki muut kentt.</Text>

	    </View>

	    </View>

	    
	    <Button style={{ margin: '16pt', padding: '8pt', backgroundColor: colors.darkRed, display: scene.state.uid.length > 0 ? 'block' : 'none' }}
	    onPress={() => {
		scene.props.onRemove(scene.state.uid);
	    }}
	    >
	    
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt', color: colors.background, display: scene.state.uid.length > 0 ? 'block': 'none' }}>
	    POISTA HOITAJA
	    </Text>
	    
	    </Button>


	    <Button style={{ margin: '8pt', padding: '8pt', backgroundColor: btnColor }}
	    onPress={() => {
		var password = scene.state.password.trim();
		if ( password.length == 0 )
		    password = null;

		var pin = scene.state.pin.trim();
		if ( pin.length == 0 )
		    pin = null;

		var email = null;
		if ( scene.state.email.trim().length > 0 &&
		     ( ! scene.props.caregiver.email ||
		       ( scene.state.email.trim() != scene.props.caregiver.email.trim() ) ) )
		    email = scene.state.email.trim();
		if ( password || pin )
		    email = scene.state.email.trim();
		
		scene.props.onUpdate(scene.state.uid, scene.state.alias, email, password, pin) }}
	    >
	    
	    <Text style={{ fontWeight: 'bold', fontSize: '10pt', color: colors.background }}>
	    TALLENNA &nbsp; &nbsp; &nbsp; &gt;
	    </Text>
	    </Button>

	    </View>
	)

    }
}
    



